import React from 'react';

import './AboutMe.scss';
import profileImage from '../../assets/profile-image.jpg';

class AboutMe extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedJob: {},
            work: [
                {
                    name: 'CSU Long Beach',
                    position: 'Software Engineer',
                    start: '2018',
                    end: '2019',
                    tech: [
                        'React', 'Angular', 'Node.js'
                    ],
                    responsibilities: [
                        'Migrated several university websites to Node.js and React/Angular',
                        'Developed a website for a university center, created logos, brochures and built the digital image of the center'
                    ]
                },
                {
                    name: 'Atos',
                    position: 'Software Engineer',
                    start: '2017',
                    end: '2018',
                    tech: [
                        'Angular', '.NET', 'Node.js'
                    ],
                    responsibilities: [
                        'Excelled in frontend and backend development with Angular, Node.js and .NET',
                        'Advanced in the use of MSSQL and MySQL',
                        'Developed various products and solutions',
                        'Strengthened technical toolkit with knowledge of web design patterns and algorithms and advanced in the area of software engineering'
                    ]
                },
                {
                    name: 'VMware',
                    position: 'Software Developer Intern',
                    start: '2016',
                    end: '2017',
                    tech: [
                        'Angular', 'Java'
                    ],
                    responsibilities: [
                        'Developed an internal company website, this includes both Frontend and Backend',
                        `Helped 20+ colleagues by replacing an obsolete QA system
                        with a lightweight web client, thus eliminating work-related
                        stress and issues and improving their workflow quality`
                    ]
                },
            ]
        };
    }

    render() {
        const jobs = this.state.work.map((job, i) => {
            return (
                <li>
                    <button class="job-button" onClick={() => this.selectJob(job)}>{job.name}</button>
                </li>
            );
        });

        return (
            <div>
                <div id="profile-container">
                    <div className="profile-container-inner">
                        <div id="profile-image-container" data-aos="fade-up-right">
                            <img id="profile-image" src={profileImage} alt="profile" />
                        </div>
                    </div>
                    <div className="profile-container-inner">
                        <p>Hey there! I'm Konstantin, a Software Engineer
                            based in Los Angeles. I develop functional websites
                    with beautiful UIs and build stable and modern APIs.</p>
                        <p>I will graduate from the Master's in Computer Science program at CSULB at the end of 2019.</p>
                        <p className="black">Technologies I work with:</p>

                        <div className="grid">
                            <div className="cell">React</div>
                            <div className="cell">.NET</div>
                            <div className="cell">Illustrator</div>
                            <div className="cell">Angular</div>
                            <div className="cell">Java</div>
                            <div className="cell">Adobe XD</div>
                            <div className="cell">Vue</div>
                            <div className="cell">SQL</div>
                            <div className="cell">InDesign</div>
                            <div className="cell">Node.js</div>
                            <div className="cell">MongoDB</div>
                            <div className="cell">Android</div>
                        </div>
                    </div>
                </div>
                <div id="work-container">
                    <ul id="job-select">
                        {jobs}
                    </ul>
                    <div>
                        <p className="black">{this.state.selectedJob.name}</p>
                    </div>
                </div>
            </div>
        );
    }

    selectJob = (job) => {
        this.setState({
            selectedJob: job
        });
    }
};

export default AboutMe;